<script setup lang="ts">
import {type BytesLike, ethers} from 'ethers';
import { computed, onMounted, ref } from 'vue';

import { useQuiz } from '../contracts';
import { Network, useEthereumStore } from '../stores/ethereum';
import AppButton from '@/components/AppButton.vue';
import SuccessInfo from '@/components/SuccessInfo.vue';
import CheckIcon from '@/components/CheckIcon.vue';
import CheckedIcon from '@/components/CheckedIcon.vue';
import UncheckedIcon from '@/components/UncheckedIcon.vue';
import QuizDetailsLoader from '@/components/QuizDetailsLoader.vue';
import {retry} from "@/utils/promise";

const props = defineProps<{ coupon: string }>();

const quiz = useQuiz();
const eth = useEthereumStore();

const errors = ref<string[]>([]);
const isLoading = ref(false);
const isCheckingAnswers = ref<Boolean>(false);
const isClaimingReward = ref<Boolean>(false);
const questions = ref<Question[]>([]);
const selectedChoices = ref<bigint[]>([]);
const allQuestionsAnswered = ref<Boolean>(false);
const correctVector = ref<boolean[]>([]);
const address = ref('');

const couponValid = ref<Boolean>(false);
const answersChecked = ref<Boolean>(false);
const answersCorrect = ref<Boolean>(false);
const rewardClaimed = ref<Boolean>(false);

interface Questions {
  questions: Question[];
}

interface Question {
  question: string;
  choices: string[];
}

function handleError(error: Error, errorMessage: string) {
  errors.value = Array();
  errors.value.push(`${errorMessage}`);
  console.error(error);
}

async function onChoiceClick(qId: number, choiceId: number): Promise<void> {
  selectedChoices.value[qId] = BigInt(choiceId);

  let allAns = true
  for (let i=0; i<selectedChoices.value.length; i++) {
    if (selectedChoices.value[i]===undefined) {
      allAns = false;
      break;
    }
  }
  allQuestionsAnswered.value = allAns;
}

async function doCheckAnswers(): Promise<void> {
  const [cv, gaslessTx] = await quiz.value!.checkAnswers(
    props.coupon,
    selectedChoices.value,
    ethers.ZeroAddress
  );
  let allCorrect = true;
  for (let i = 0; i < cv.length; i++) {
    if (!cv[i]) {
      allCorrect = false;
      break;
    }
  }
  answersChecked.value = true;
  answersCorrect.value = allCorrect;
  correctVector.value = cv;
}

async function fetchQuestions(): Promise<void> {
  try {
    isLoading.value = true;
    questions.value = await quiz.value!.getQuestions(props.coupon);
    selectedChoices.value = Array(questions.value.length); // prepare an array of undefined values until the answer is selected.
    couponValid.value = true;
  } catch(e) {
    handleError(e as Error, 'Kupon ni veljaven');
  } finally {
    isLoading.value = false;
  }
}

async function claimReward(e: Event): Promise<void> {
  if (e.target instanceof HTMLFormElement) {
    e.target.checkValidity();
    if (!e.target.reportValidity()) return;
  }

  e.preventDefault();

  try {
    isClaimingReward.value = true;
    console.log(address.value);
    const [cv, gaslessTx] = await quiz.value!.checkAnswers(
        props.coupon,
        selectedChoices.value,
        ethers.getAddress(address.value),
    );
    console.log(cv);
    console.log(gaslessTx);
    let receipt = await (await eth.provider.broadcastTransaction(gaslessTx)).wait(); // gasless version
    //let receipt = await (await quiz.value!.claimReward(gaslessTx)).wait(); // standard version
    console.log("receipt.status: "+receipt!.status);
    if (receipt!.status==1) {
      rewardClaimed.value = true
    }
  } catch (e: any) {
    handleError(e, 'Nagrade ni bilo mogoƒçe prejeti');
  } finally {
    isClaimingReward.value = false;
  }
}

async function checkAnswers(e: Event): Promise<void> {
  e.preventDefault();
  try {
    isCheckingAnswers.value = true;
    await doCheckAnswers();
  } catch (e: any) {
    handleError(e.reason, e.message);
  } finally {
    isCheckingAnswers.value = false;
  }
}

onMounted(async () => {
  await fetchQuestions();
});
</script>

<template>
  <div v-if="errors.length > 0" class="text-red-500 px-3 mt-5 rounded-xl-sm">
    <span class="font-bold">Napaka:</span>
    <div v-for="error in errors" :key="error">{{ error }}</div>
  </div>
  <section v-if="couponValid && !answersCorrect">
    <div v-if="questions">
      <form @submit="checkAnswers">
        <fieldset
            class="mb-5"
            v-for="[qId, question] in Object.entries(questions)"
            :key="qId"
        >
          <p style="cursor:default" class="text-white text-base mb-5">{{parseInt(qId)+1}}. {{question.question}}
            <span v-if="answersChecked">
              <span v-if="correctVector[qId]">‚úÖ</span>
              <span v-if="!correctVector[qId]">‚ùå</span>
            </span>
          </p>
          <AppButton
              v-for="(choice, choiceId) in question.choices"
              :key="choiceId"
              :class="{
              selected: selectedChoices[qId] === choiceId,
              'pointer-events-none': isLoading,
            }"
              class="choice-btn mb-2 w-full"
              variant="choice"
              @click="onChoiceClick(qId, choiceId)"
          >
            <span class="flex gap-2">
              <div class="align-middle">
                <CheckedIcon v-if="selectedChoices[qId] === BigInt(choiceId)" />
                <UncheckedIcon v-else />
              </div>
              <span class="leading-6 normal-case text-left">{{ choice }}</span>
            </span>
          </AppButton>
        </fieldset>

        <div v-if="errors.length > 0" class="text-red-500 px-3 mt-5 rounded-xl-sm">
          <span class="font-bold">Napaka:</span>
          <div v-for="error in errors" :key="error">{{ error }}</div>
        </div>
        <div class="flex justify-between items-start mt-6 mb-20">
          <AppButton
            type="submit"
            variant="primary"
            :disabled="isLoading || !allQuestionsAnswered"
            @click="checkAnswers"
          >
            <span v-if="isCheckingAnswers">Preverjam odgovore‚Ä¶</span>
            <span v-else>Preveri odgovore</span>
          </AppButton>
        </div>
      </form>
    </div>

    <QuizDetailsLoader v-else />
  </section>
  <section class="pt-5"  v-if="answersCorrect && !rewardClaimed">
    <SuccessInfo>
      <h2 class="text-2xl text-white text-base mb-5 mt-10">Kviz ste uspe≈°no re≈°ili!</h2>
    </SuccessInfo>

    <p class="text-white text-base mb-5 mt-10">
      ƒåe ≈æelite prejeti nagrado, v spodnje polje vpi≈°ite naslov va≈°e kriptodenarnice.
      Kovanƒçke ROSE boste prejeli na verigi <a href="https://docs.oasis.io/dapp/sapphire/#chain-information" target="_blank">Oasis Sapphire Mainnet</a>.
    </p>
    <form @submit="claimReward">
      <div class="form-group">
        <input
            type="text"
            id="addressText"
            class="peer"
            placeholder=" "
            v-model="address"
            pattern="^(0x)?[0-9a-fA-F]{40}$"
            required
        />
        <label
            for="addressText"
            class="peer-focus:text-primaryDark peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-5"
        >
          Va≈° naslov (0x...):
          <span class="text-red-500">*</span>
        </label>
      </div>
      <details class="text-white mb-5">
        <summary class="cursor-pointer mb-3 text-right">Kaj je kriptodenarnica?</summary>
        <p class="ml-3.5">
          Kriptodenarnica hrani va≈° zasebni kljuƒç, s katerim podpisujete razliƒçne
          transakcije na omre≈æju, na primer za po≈°iljanje va≈°ega virtualnega
          denarja. Denarnica je lahko v obliki
          samostojne aplikacije na raƒçunalniku ali telefonu, kot raz≈°iritev za
          spletni brskalnik, lahko pa je samostojna naprava, saj je s tem najbolj
          odporna na zlonamerno kodo, ki bi lahko odtujila va≈° zasebni kljuƒç.</p>
        <p class="ml-3.5 mb-10">
          ƒåe ste zaƒçetnik in zasebnega kljuƒça ≈°e nimate, predlagamo uporabo
          denarnice <a href="https://metamask.io/download/" target="_blank">Metamask</a>.
          Po namestitvi ne pozabite povezati denarnice z omre≈æjem Oasis Sapphire. To
          najla≈æje storite s pritiskom na enega od gumbov "Add to Metamask" v seznamu
          <a href="https://docs.oasis.io/dapp/sapphire/#rpc-endpoints" target="_blank">dostopnih toƒçk Mainnet</a>.
        </p>
      </details>

      <AppButton class="no-capitalize" type="submit" variant="primary" :disabled="isClaimingReward">
        <span class="normal-case" v-if="isClaimingReward">Generiram transakcijo in po≈°iljam nagrado‚Ä¶</span>
        <span class="normal-case" v-else>Prejmi nagrado</span>
      </AppButton>
    </form>
  </section>
  <section v-if="rewardClaimed">
    <SuccessInfo>
      <h3 class="text-white text-3xl mb-10">Hvala za sodelovanje v nagradnem kvizu!</h3>
      <p class="text-white">
        Vabljeni, da sprogramirate <a href="https://docs.oasis.io/dapp/sapphire/quickstart" target="_blank">va≈°o prvo aplikacijo na omre≈æju Oasis Sapphire</a>.
        Lahko pa kovanƒçke pretopite v <span style="text-decoration: line-through;">pivo</span> sok zate in za tvoje prijatelje v bli≈ænjem kripto lokalu.
        ƒåe potrebujete pomoƒç ali pa ≈æelite svoje znanje deliti ostalim, se pridru≈æite slovenski skupnosti Oasis na <a href="https://discord.com/invite/oasisprotocol" target="_blank">Discord kanalu #slovenia üá∏üáÆ </a>.
      </p>
    </SuccessInfo>
  </section>
</template>

<style lang="postcss" scoped>
.error {
  @apply text-red-500;
}
